@import "variables";

/* Footer */
.footer {
	background-color: $color-grey-light;
	padding: 3rem 15%;
}

.footer .heading {
	display: flex;
	align-items: center;
	justify-content: space-around;
	color: #232323;
	font-size: 3.5rem;
	font-weight: 800;
}

.footer__line {
	width: 20%;
	height: 0.4rem;
	background-color: $color-primary-light;
}

.footer .social__link {
	margin: 0 2rem;
}

.footer .social__link svg {
	width: 4rem;
	height: 4rem;
}

.footer .social__link svg path {
	fill: #444444;
	opacity: 0.25;
}

.footer .social__link:hover svg path {
	opacity: 0.5;
}

.footer__copy {
	font-size: 1.5rem;
	text-align: center;
	margin-top: 4rem;
}
